import React, { Component } from "react";
import ReactSVG from "react-svg";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import articleCoverFirst from '../../images/article-cover-1.webp';
import articleCoverSecond from '../../images/article-cover-2.webp';
import loadMoreArticles from '../../images/load-more-articles.svg';

class FifthBlock extends Component {
  render() {    
    return (
        <section className='cases' id='cases'>
          <ul>
            <li style={{backgroundImage: `url("${articleCoverFirst}")`}}>
              <Link to='/case/swimming-pool'>СМИ о нас</Link>
            </li>
            <li style={{backgroundImage: `url("${articleCoverSecond}")`}}>
              <Link to='/case/business-school'>СМИ о нас</Link>
            </li>
            <li className="link-to-journal">
              <a href='https://google.com'>СМИ о нас</a>            
            </li>
            <li style={{backgroundImage: `url("${articleCoverFirst}")`}}>
              <Link to='/case/'>СМИ о нас</Link>
            </li>
            <li style={{backgroundImage: `url("${articleCoverSecond}")`}}>
              <Link to='/case/'>СМИ о нас</Link>
            </li>
            <li className="load-more-articles">              
              <ReactSVG src={loadMoreArticles}/>
              <p>загрузить еще</p>              
            </li>
          </ul>
        </section>
    );
  }
}

export default FifthBlock;
