import React, { Component } from "react";
import Data from "../case-template";
import logo from "../../images/logo.svg";
import downArrow from "../../images/down-arrow.svg";
import ReactSVG from "react-svg";
import quotes from "../../images/quotes.png";
import NinethBlock from "./nineth-block";
import AnchorLink from "react-anchor-link-smooth-scroll";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

class Case extends Component {
  constructor(props) {
    super(props);
    this.data = null;
    switch (this.props.match.params.token) {
      case "swimming-pool":
        this.data = Data.swimmingPool;
        break;
      case "business-school":
        this.data = Data.businessScholl;
        break;
      default:
        this.data = Data.swimmingPool;
        break;
    }

    this.state = {
      visible: false,
      active: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0)
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    if (window.pageYOffset > 600) {
        this.setState({visible: true});
    } else {
        this.setState({visible: false})
    }
  }

  render() {
    return (
      <React.Fragment>
        <header
          className="case-header"
          id="case-header"
          style={{
            background: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${this.data.headerBackground}')`,
            backgroundSize: "cover",
            backgroundPosition: "center center",
            backgroundRepeat: "no-repeat"
          }}
        >
          <div className="case-header-content">
            <nav className="case-nav">
              <ul>
                <li>
                  <ReactSVG src={logo} />
                </li>
                <li>
                  <Link to="/">на главную</Link>
                </li>
              </ul>
            </nav>
            <div className="case-header-content-text">
              {this.data.caseTitle.map((item, index) => (
                <h2 key={index}>{item}</h2>
              ))}
              {this.data.caseSubtitle.map((item, index) => (
                <p key={index}>{item}</p>
              ))}
              <ul className='case-header-hashtags'>
                <li>#стратегия</li>
                <li>#дизайн</li>
                <li>#реклама</li>
              </ul>
            </div>
            <div className="case-arrow-down">
              <AnchorLink href="#case-main">
                <img src={downArrow} alt="down-arrow" />
              </AnchorLink>
            </div>
          </div>
        </header>
        <main className="case-main" id="case-main">
          <div className="wrapper case-wrapper">
            <section className="info-and-goal">
              <div className="pool-image">
                <img src={this.data.secondBlockImage} />
              </div>
              <div className="info-and-goal-content">
                <ul>
                  <li>
                    <h3>Информация о клиенте</h3>
                    <p>{this.data.clientInfo}</p>
                  </li>
                  <li>
                    <h3>Цели и задачи компании</h3>
                    <p>{this.data.goals}</p>
                  </li>
                </ul>
              </div>
            </section>
            <section className="case-solution">
              <h3>Решение</h3>
              {this.data.solution.map((item, index) => {
                return (
                  <div className='case-solution-stage'>
                    <span>
                      {index + 1 > 9 ? `${index + 1}` : `0${index + 1}`}
                    </span>
                    {item.map((secondItem, secondIndex) => {
                      if (typeof secondItem === "object") {
                        return (
                          <div
                            className="case-solution-image"
                            key={secondIndex}
                          >
                            {secondItem.map(item => {
                              if (item.split(" ")[0] !== "p") {
                                return <img src={item} alt="картинка" />;
                              } else {
                                return (
                                  <p className="case-solution-image-title">
                                    {item
                                      .split(" ")
                                      .splice(1)
                                      .join(" ")}
                                  </p>
                                );
                              }
                            })}
                          </div>
                        );
                        // if you need a block with text
                      } else if (secondItem.split(" ")[0] === "div") {
                        let textBlock = null;
                        let message = secondItem.split(" ").splice(1);
                        // checking for bold text
                        if (message.indexOf("span") !== -1) {
                          let firstIndex = message.indexOf("span");
                          let secondIndex = message.lastIndexOf("span");
                          let textBeforeSpan = message
                            .slice(0, firstIndex)
                            .join(" ");
                          let textAfterSpan = message
                            .slice(secondIndex + 1)
                            .join(" ");
                          let innerText = message
                            .splice(
                              firstIndex + 1,
                              secondIndex - firstIndex - 1
                            )
                            .join(" ");
                          textBlock = (
                            <p>
                              {textBeforeSpan}
                              <span> {innerText} </span>
                              {textAfterSpan}
                            </p>
                          );
                        } else {
                          textBlock = <p>{message.join(" ")}</p>;
                        }
                        return (
                          <div
                            className="case-solution-block"
                            key={secondIndex}
                          >
                            {textBlock}
                          </div>
                        );
                        // if you put 'h'
                      } else if (secondItem.split(" ")[0] === "h"){
                        return (
                          <h2 className='header-for-stage'>{secondItem.split(' ').splice(1).join(' ')}</h2>
                        )
                      } else {
                        // as a result only UL elements left
                        return (
                          <div
                            className="case-solution-block"
                            key={secondIndex}
                          >
                            {secondItem
                              .split("li")
                              .filter(item => item.length !== 0)
                              .map(item => {
                                return <p>{`- ${item}`}</p>;
                              })}
                          </div>
                        );
                      }
                    })}
                  </div>
                );
              })}
            </section>
          </div>
          <section
            className="case-result we-in-numbers"
            style={{ background: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${this.data.resultsBackground}')`, backgroundSize: 'cover', backgroundPosition: 'center center' }}
          >
            <div className="wrapper case-wrapper">
              <h2>Результат</h2>
              <ul>
                {this.data.result.map(item => {
                  let strings = item.split(" ");
                  let text = strings[0];
                  let span = strings.slice(1).join(" ");
                  return (
                    <li>
                      <p>{text}</p>
                      <span>{span}</span>
                    </li>
                  );
                })}
              </ul>
            </div>
          </section>
          <div className="wrapper case-wrapper">
            <section className="case-other-inforamtion">
              {this.data.otherInfo.map((item, index) => {
                if (typeof item === "object") {
                  return (
                    <div className="case-solution-image" key={item}>
                      {item.map(item => {
                        if (item.split(" ")[0] !== "p") {
                          return <img src={item} alt="картинка" />;
                        } else {
                          return (
                            <p className="case-solution-image-title">
                              {item
                                .split(" ")
                                .splice(1)
                                .join(" ")}
                            </p>
                          );
                        }
                      })}
                    </div>
                  );
                  // if you need a block with text
                } else if (item.split(" ")[0] === "div") {
                  let textBlock = null;
                  let message = item.split(" ").splice(1);
                  // checking for bold text
                  if (message.indexOf("span") !== -1) {
                    let firstIndex = message.indexOf("span");
                    let secondIndex = message.lastIndexOf("span");
                    let textBeforeSpan = message.slice(0, firstIndex).join(" ");
                    let textAfterSpan = message
                      .slice(secondIndex + 1)
                      .join(" ");
                    let innerText = message
                      .splice(firstIndex + 1, secondIndex - firstIndex - 1)
                      .join(" ");
                    textBlock = (
                      <p>
                        {textBeforeSpan}
                        <span> {innerText} </span>
                        {textAfterSpan}
                      </p>
                    );
                  } else {
                    textBlock = <p>{message.join(" ")}</p>;
                  }
                  return (
                    <div className="case-solution-block" key={index}>
                      {textBlock}
                    </div>
                  );
                } else if (item.split(" ")[0] === "h"){
                  return (
                    <h2 className='header-for-stage'>{item.split(' ').splice(1).join(' ')}</h2>
                  )
                } else if (item.split(" ")[0] === "quotes") {
                  let text = item.split(" ").slice(1);
                  return (
                    <div
                      className="case-quotes-block"
                      style={{ background: `url('${quotes}')` }}
                    >
                      <p>{text.join(" ")}</p>
                    </div>
                  );
                } else {
                  // as a result only UL elements left
                  return (
                    <div
                      className="case-solution-block"
                      key={item}
                    >
                      {item
                        .split("li")
                        .filter(item => item.length !== 0)
                        .map(item => {
                          return <p>{`- ${item}`}</p>;
                        })}
                    </div>
                  );
                }
              })}
            </section>
            <section className="workers">
              <h2>Над проектом работали</h2>
              <div className='workers-content'>
              {this.data.workers.map((item, index) => {
                if (item.indexOf("image") === -1) {
                  return (
                    <div className="single-worker">
                      <div className='single-worker-image'>
                        <img src={this.data.workers[index + 1]} alt="worker" />
                      </div>
                      <div className="single-worker-info">
                        <h4>{item.split(",")[0]}</h4>
                        <p>{item.split(",")[1]}</p>
                      </div>
                    </div>
                  );
                }
              })}                        
                <ul className='case-workers-hashtags'>
                  <li>#стратегия</li>
                  <li>#дизайн</li>
                  <li>#реклама</li>
                </ul> 
              </div>  
            </section>
          </div>
        </main>
        <NinethBlock/>
        <AnchorLink href="#case-header" className={`arrow-to-up ${this.state.visible ? 'visible' : ''}`}>
            &#8593;
        </AnchorLink>
      </React.Fragment>
    );
  }
}

export default Case;
