import React, { Component } from "react";

import FirstBlock from "./first-block";
import SecondBlock from "./second-block";
import ThirdBlock from './third-block';
import FourthBlock from './fourth-block';
import FifthBlock from './fifth-block';
import SixthBlock from './sixth-block';
import SeventhBlock from './seventh-block';
import EighthBlock from './eighth-block';
import NinethBlock from './nineth-block.js';

export default class Landing extends Component {
    render() {
        return (    
        <React.Fragment>
            <div className='wrapper'>
                <FirstBlock />
            </div>
            <SecondBlock />
            <div className='wrapper'>
            <main className="main" id="main">
                <ThirdBlock />
                <FourthBlock />
                <FifthBlock />
                <SixthBlock />
                <SeventhBlock />
                <EighthBlock />
            </main>
            </div>
            <NinethBlock /> 
        </React.Fragment>        
        )
    }
}